import Vue from 'vue';
import { faUser, faUsers } from '@fortawesome/free-solid-svg-icons';
import i18n from '../../../i18n';
import getAcct from '../../../../../misc/acct/render';
export default Vue.extend({
    i18n: i18n('common/views/components/messaging.vue'),
    props: {
        compact: {
            type: Boolean,
            default: false
        },
        headerTop: {
            type: Number,
            default: 0
        }
    },
    data() {
        return {
            fetching: true,
            moreFetching: false,
            messages: [],
            q: null,
            result: [],
            connection: null,
            faUser, faUsers
        };
    },
    mounted() {
        this.connection = this.$root.stream.useSharedConnection('messagingIndex');
        this.connection.on('message', this.onMessage);
        this.connection.on('read', this.onRead);
        this.$root.api('messaging/history', { group: false }).then(userMessages => {
            this.$root.api('messaging/history', { group: true }).then(groupMessages => {
                const messages = userMessages.concat(groupMessages);
                messages.sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime());
                this.messages = messages;
                this.fetching = false;
            });
        });
    },
    beforeDestroy() {
        this.connection.dispose();
    },
    methods: {
        getAcct,
        isMe(message) {
            return message.userId == this.$store.state.i.id;
        },
        onMessage(message) {
            if (message.recipientId) {
                this.messages = this.messages.filter(m => !((m.recipientId == message.recipientId && m.userId == message.userId) ||
                    (m.recipientId == message.userId && m.userId == message.recipientId)));
                this.messages.unshift(message);
            }
            else if (message.groupId) {
                this.messages = this.messages.filter(m => m.groupId !== message.groupId);
                this.messages.unshift(message);
            }
        },
        onRead(ids) {
            for (const id of ids) {
                const found = this.messages.find(m => m.id == id);
                if (found) {
                    if (found.recipientId) {
                        found.isRead = true;
                    }
                    else if (found.groupId) {
                        found.reads.push(this.$store.state.i.id);
                    }
                }
            }
        },
        search() {
            if (this.q == '') {
                this.result = [];
                return;
            }
            this.$root.api('users/search', {
                query: this.q,
                localOnly: false,
                limit: 10,
                detail: false
            }).then(users => {
                this.result = users.filter(user => user.id != this.$store.state.i.id);
            });
        },
        navigate(user) {
            this.$emit('navigate', user);
        },
        navigateGroup(group) {
            this.$emit('navigateGroup', group);
        },
        onSearchKeydown(e) {
            switch (e.which) {
                case 9: // [TAB]
                case 40: // [↓]
                    e.preventDefault();
                    e.stopPropagation();
                    this.$refs.searchResult.childNodes[0].focus();
                    break;
            }
        },
        onSearchResultKeydown(i, e) {
            const list = this.$refs.searchResult;
            const cancel = () => {
                e.preventDefault();
                e.stopPropagation();
            };
            switch (true) {
                case e.which == 27: // [ESC]
                    cancel();
                    this.$refs.search.focus();
                    break;
                case e.which == 9 && e.shiftKey: // [TAB] + [Shift]
                case e.which == 38: // [↑]
                    cancel();
                    (list.childNodes[i].previousElementSibling || list.childNodes[this.result.length - 1]).focus();
                    break;
                case e.which == 9: // [TAB]
                case e.which == 40: // [↓]
                    cancel();
                    (list.childNodes[i].nextElementSibling || list.childNodes[0]).focus();
                    break;
            }
        },
        async startUser() {
            const { result: user } = await this.$root.dialog({
                user: {
                    local: true
                }
            });
            if (user == null)
                return;
            this.navigate(user);
        },
        async startGroup() {
            const groups1 = await this.$root.api('users/groups/owned');
            const groups2 = await this.$root.api('users/groups/joined');
            const { canceled, result: group } = await this.$root.dialog({
                type: null,
                title: this.$t('select-group'),
                select: {
                    items: groups1.concat(groups2).map(group => ({
                        value: group, text: group.name
                    }))
                },
                showCancelButton: true
            });
            if (canceled)
                return;
            this.navigateGroup(group);
        }
    }
});
